'use client';

import { ThemeProvider as MUIThemeProvider, createTheme } from '@mui/material';
import CssBaseline from '@mui/material/CssBaseline';
import { createContext, useEffect, useState, useContext } from 'react';

type Theme = 'light' | 'dark' | 'system';

interface ThemeContextProps {
  theme: Theme;
  setTheme: (theme: Theme) => void;
}

// Create the context with a default value
const defaultThemeContext: ThemeContextProps = {
  theme: 'system',
  setTheme: () => console.warn('Theme context not initialized')
};

const ThemeContext = createContext<ThemeContextProps>(defaultThemeContext);

// Exports the context for direct use if needed
export { ThemeContext };

export function ThemeProvider({ children }: { children: React.ReactNode }) {
  const [theme, setTheme] = useState<Theme>('system');
  const [mounted, setMounted] = useState(false);
  const [mode, setMode] = useState<'light' | 'dark'>('light');

  // Initial setup effect - runs only once
  useEffect(() => {
    // Set mounted to true when component mounts
    setMounted(true);
    
    // Load theme from localStorage if available
    const savedTheme = localStorage.getItem('theme') as Theme;
    if (savedTheme) {
      setTheme(savedTheme);
      applyTheme(savedTheme);
    } else {
      // Default to system preference
      setTheme('system');
      applyTheme('system');
    }

    // Add an event listener for system preference changes
    const mediaQuery = window.matchMedia('(prefers-color-scheme: dark)');
    const handleChange = () => {
      if (theme === 'system') {
        applyTheme('system');
      }
    };

    mediaQuery.addEventListener('change', handleChange);
    return () => mediaQuery.removeEventListener('change', handleChange);
  }, []); // Empty dependency array so this only runs once

  // Effect to apply theme changes
  useEffect(() => {
    if (mounted) {
      applyTheme(theme);
    }
  }, [theme, mounted]);

  const applyTheme = (selectedTheme: Theme) => {
    if (typeof document === 'undefined') return; // Skip during SSR
    
    const root = document.documentElement;
    const body = document.body;
    
    // Remove existing theme classes
    root.classList.remove('dark-theme', 'light-theme', 'system-theme');
    root.removeAttribute('data-theme');
    body.removeAttribute('data-theme');
    
    let newMode: 'light' | 'dark' = 'light';
    
    if (selectedTheme === 'dark') {
      root.classList.add('dark-theme');
      newMode = 'dark';
      root.setAttribute('data-theme', 'dark');
      body.setAttribute('data-theme', 'dark');
    } else if (selectedTheme === 'light') {
      root.classList.add('light-theme');
      newMode = 'light';
      root.setAttribute('data-theme', 'light');
      body.setAttribute('data-theme', 'light');
    } else if (selectedTheme === 'system') {
      // Check system preference
      const prefersDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
      if (prefersDark) {
        root.classList.add('dark-theme');
        newMode = 'dark';
        root.setAttribute('data-theme', 'dark');
        body.setAttribute('data-theme', 'dark');
      } else {
        root.classList.add('light-theme');
        newMode = 'light';
        root.setAttribute('data-theme', 'light');
        body.setAttribute('data-theme', 'light');
      }
      root.classList.add('system-theme');
    }
    
    setMode(newMode);
  };

  const setThemeValue = (newTheme: Theme) => {
    console.log('Setting theme to:', newTheme);
    
    // First update localStorage
    localStorage.setItem('theme', newTheme);
    
    // Then update the state
    setTheme(newTheme);
    
    // Force DOM updates in case of issues with state synchronization
    document.documentElement.dataset.themeLoading = 'true';
    setTimeout(() => {
      applyTheme(newTheme);
      document.documentElement.dataset.themeLoading = 'false';
    }, 0);
  };

  // Create MUI theme based on current mode
  const lightTheme = createTheme({
    palette: {
      mode: 'light',
      primary: {
        main: '#006FEE',
        light: '#60A5FA',
        dark: '#2563EB',
        contrastText: '#ffffff',
      },
      secondary: {
        main: '#71717A',
        light: '#A1A1AA',
        dark: '#52525B',
        contrastText: '#ffffff',
      },
      background: {
        default: '#ffffff',
        paper: '#ffffff',
      },
      text: {
        primary: '#11181C',
        secondary: '#71717A',
      },
    },
    components: {
      MuiPaper: {
        styleOverrides: {
          root: {
            backgroundImage: 'none',
            backgroundColor: '#ffffff',
            boxShadow: '0 2px 10px rgba(0, 0, 0, 0.05)',
            '&:hover': {
              boxShadow: '0 4px 20px rgba(0, 0, 0, 0.1)',
            },
          },
        },
      },
      MuiButton: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            borderRadius: '8px',
            fontWeight: 500,
          },
          contained: {
            boxShadow: 'none',
            '&:hover': {
              boxShadow: '0 4px 12px rgba(0, 111, 238, 0.2)',
            },
          },
        },
      },
      MuiTextField: {
        styleOverrides: {
          root: {
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#e4e4e7',
              },
              '&:hover fieldset': {
                borderColor: '#006FEE',
              },
              '&.Mui-focused fieldset': {
                borderColor: '#006FEE',
              },
            },
          },
        },
      },
      MuiDrawer: {
        styleOverrides: {
          paper: {
            backgroundColor: '#ffffff',
            borderRight: '1px solid #e0e0e0',
          },
        },
      },
      MuiListItem: {
        styleOverrides: {
          root: {
            '&.Mui-selected': {
              backgroundColor: '#e8f0fe',
              color: '#006FEE',
              '&:hover': {
                backgroundColor: '#e8f0fe',
              },
            },
            '&:hover': {
              backgroundColor: '#f5f5f5',
            },
          },
        },
      },
    },
  });

  const darkTheme = createTheme({
    palette: {
      mode: 'dark',
      primary: {
        main: '#60A5FA',
        light: '#93C5FD',
        dark: '#3B82F6',
        contrastText: '#ffffff',
      },
      secondary: {
        main: '#A1A1AA',
        light: '#D4D4D8',
        dark: '#71717A',
        contrastText: '#ffffff',
      },
      background: {
        default: '#09090b',
        paper: '#18181b',
      },
      text: {
        primary: '#ffffff',
        secondary: '#A1A1AA',
      },
    },
    components: {
      MuiPaper: {
        styleOverrides: {
          root: {
            backgroundImage: 'none',
            backgroundColor: '#18181b',
            boxShadow: '0 2px 10px rgba(0, 0, 0, 0.2)',
            '&:hover': {
              boxShadow: '0 4px 20px rgba(0, 0, 0, 0.4)',
            },
          },
        },
      },
      MuiButton: {
        styleOverrides: {
          root: {
            textTransform: 'none',
            borderRadius: '8px',
            fontWeight: 500,
          },
          contained: {
            boxShadow: 'none',
            '&:hover': {
              boxShadow: '0 4px 12px rgba(96, 165, 250, 0.3)',
            },
          },
        },
      },
      MuiTextField: {
        styleOverrides: {
          root: {
            '& .MuiOutlinedInput-root': {
              '& fieldset': {
                borderColor: '#27272a',
              },
              '&:hover fieldset': {
                borderColor: '#60A5FA',
              },
              '&.Mui-focused fieldset': {
                borderColor: '#60A5FA',
              },
            },
          },
        },
      },
      MuiDrawer: {
        styleOverrides: {
          paper: {
            backgroundColor: '#18181b',
            borderRight: '1px solid #27272a',
          },
        },
      },
      MuiListItem: {
        styleOverrides: {
          root: {
            '&.Mui-selected': {
              backgroundColor: '#1e293b',
              color: '#60A5FA',
              '&:hover': {
                backgroundColor: '#1e293b',
              },
            },
            '&:hover': {
              backgroundColor: '#27272a',
            },
          },
        },
      },
    },
  });

  // Prevent flash of unstyled content
  if (!mounted) {
    return <>{children}</>;
  }

  return (
    <ThemeContext.Provider value={{ theme, setTheme: setThemeValue }}>
      <MUIThemeProvider theme={theme === 'dark' ? darkTheme : lightTheme}>
        <CssBaseline />
        {children}
      </MUIThemeProvider>
    </ThemeContext.Provider>
  );
}

// Custom hook for using the theme context
export function useTheme() {
  // Context now always has a value due to default context
  return useContext(ThemeContext);
} 